.document-table {
  a {
    color: var(--gray-800, black);
  }

  td {
    vertical-align: middle;
  }

  tr {
    &.archived {
      a {
        color: var(--gray-600, black);
      }
    }
  }
}

.document-search-form {
  position: relative;

  .icon-wrapper {
    position: absolute;
    color: var(--gray-600);

    svg {
      width: var(--icon-size, 18px);
      height: var(--icon-size, 18px);
    }
  }

  input {
    border: 1px solid var(--gray-100);
    background-color: var(--gray-100, white);
    padding-left: 36px;

    &:focus {
      background-color: white;
    }
  }
}

.document-list-view {
  .document-list-link {
    color: var(--gray-900);
    display: block;
    text-decoration: none;
    padding: var(--sp-1);
    margin-bottom: var(--sp-0);

    background-color: var(--gray-100);
    border-radius: var(--border-radius);

    &:hover {
      background-color: var(--blue-50);
    }

    .document-title {
      margin-bottom: var(--sp-0);
    }
    .document-meta {
      display: flex;
      align-items: center;
      font-size: var(--fs-sm);

      .count {
        margin-right: 4px;
      }
    }
  }
}
